import React, { useState, useEffect } from 'react';
import * as lab from '@material-ui/lab';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import AddIcon from '@material-ui/icons/Add';
import { MenuItem, Checkbox, TextField, Grid, Paper } from '@material-ui/core';
import { format } from 'date-fns'
import { deleteIatacode } from "../../crud/iatacodes.crud";
import Notification from '../Notification'

import DateFnsUtils from '@date-io/date-fns'; // choose your lib
import {
    DateTimePicker,
    MuiPickersUtilsProvider,
} from '@material-ui/pickers';


const FormCreate = ({ disabled, id, disableStart, disableEnd, options, onValueChange, onAddMore, locationName, definedCode, onDelete }) => {
    const [optSelected, setOptSelected] = useState(options.dictionary[locationName] ?? null);
    const [formId] = useState(Math.random());
    const [inputCode, setInputCode] = useState(definedCode ?? '');
    const [success, setSuccess] = useState(false);

    const [dataToSend, setDataToSend] = useState({
        disabled: disabled,
        startDate: disableStart,
        endDate: disableEnd,
        iataCode: definedCode || null,
        iataCodeId: optSelected ? optSelected.value : null
    });

    const TriggerChange = (s) => {
        if ((s.iataCode !== null && s.iataCode !== undefined) &&
            (s.iataCodeId !== null && s.iataCodeId !== undefined)) {
            onValueChange({ ...s , id })
        }
    }

    useEffect(() => {
        TriggerChange(dataToSend)
    }, [dataToSend])

    return (
        <Grid item>
            {success && (
                <Notification msg="Code deleted!" />
            )}
            <Paper>
                <Grid container spacing={1} alignItems="center">

                    <Grid item xs={3}>
                        <lab.Autocomplete
                            id="combo-box-demo"
                            defaultValue={optSelected ? optSelected : null}
                            options={options.list}
                            getOptionLabel={(option) => `${option.location} - (${option.code})`}
                            renderInput={(params) => <TextField {...params} label="Select Pickup Location" variant="outlined" />}
                            onInputChange={(event, value) => {                                
                                if (!value) return

                                // we split the code from the location assuming this format: Arrabury - (AAB)
                                let code = value.split('-')[1];
                                if (!code) return
                                // we delete parentesis and trim the string to delete whitespace
                                code = code.replace(/\(|\)/g,'').trim();
                                const suggestion = options.dictionary[code]
                                if (!suggestion) return
                                setOptSelected(suggestion)

                                setDataToSend(prev => {
                                    const newState = {
                                        ...prev,
                                        iataCodeId: suggestion.code,
                                    }
                                    return newState
                                })
                            }}
                        />
                    </Grid>
                    <Grid item xs={2}>
                        <TextField
                            value={inputCode}
                            onChange={(e) => {
                                e.persist();
                                setInputCode(e.target.value)
                                setDataToSend(prev => {
                                    const newState = {
                                        ...prev,
                                        iataCode: e.target.value
                                    }
                                    return newState
                                })
                            }} placeholder="Internal Code" />
                    </Grid>

                    <Grid item>
                        <MuiPickersUtilsProvider utils={DateFnsUtils}>
                            <DateTimePicker style={{ width: '12rem', margin: 0 }} value={dataToSend.startDate || null} onChange={(d) => {
                                setDataToSend(prev => {
                                    const newState = {
                                        ...prev,
                                        startDate: format(d, `yyyy-MM-dd'T'hh:mm:00`),
                                    }
                                    return newState;
                                })

                            }} />
                            <DateTimePicker style={{ width: '12rem', margin: 0 }} value={dataToSend.endDate || null} onChange={(d) => {
                                setDataToSend(prev => {

                                    const newState = {
                                        ...prev,
                                        endDate: format(d, `yyyy-MM-dd'T'hh:mm:00`),
                                    }
                                    return newState;
                                })

                            }} />
                        </MuiPickersUtilsProvider>
                    </Grid>
                    <Grid item style={{ cursor: 'pointer' }} >
                        <FormControlLabel
                            control={<Checkbox
                                checked={(dataToSend.startDate !== null && dataToSend.startDate !== undefined && dataToSend.endDate !== null && dataToSend.endDate !== undefined) || dataToSend.disabled}
                                onChange={() => {
                                    setDataToSend(prev => ({ ...prev, disabled: !prev.disabled }))
                                }} />}
                            label="Disabled"
                        />
                    </Grid>
                    <Grid item xs={1} style={{ cursor: 'pointer', display: 'flex', justifyContent: 'center' }} onClick={onAddMore} >
                        <AddIcon />
                    </Grid>
                    <Grid item
                        xs={1}
                        style={{ cursor: 'pointer', display: 'flex', justifyContent: 'center' }}
                        onClick={() => {
                            if (!optSelected) return
                            deleteIatacode(id)
                                .then(() => {
                                    onDelete()
                                        .then(() => {
                                            setSuccess(true);
                                        })
                                })
                        }}


                    >
                        <DeleteForeverIcon />
                    </Grid>
                </Grid>
            </Paper>
        </Grid>
    );
}

export default FormCreate;