import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { TextField, Grid, useMediaQuery, useTheme, Typography, Select, MenuItem } from '@material-ui/core';
import { VariableSizeList } from 'react-window';
import * as lab from '@material-ui/lab';
import { getLocations as getAllLocation } from "../../crud/locations.crud";
import ListSubheader from '@material-ui/core/ListSubheader';
import { connect } from "react-redux";
import { FormattedMessage, injectIntl } from "react-intl";
import AxioHook from 'axios-hooks'
import { postLocation } from "../../crud/locations.crud";
import {
  Button, Container, Row, Col
} from "react-bootstrap";
import { useAppState } from '../AppState';

const LISTBOX_PADDING = 8; // px

function renderRow(props) {
  const { data, index, style } = props;
  return React.cloneElement(data[index], {
    style: {
      ...style,
      top: style.top + LISTBOX_PADDING,
    },
  });
}

const OuterElementContext = React.createContext({});

const OuterElementType = React.forwardRef((props, ref) => {
  const outerProps = React.useContext(OuterElementContext);
  return <div ref={ref} {...props} {...outerProps} />;
});

function useResetCache(data) {
  const ref = React.useRef(null);
  React.useEffect(() => {
    if (ref.current != null) {
      ref.current.resetAfterIndex(0, true);
    }
  }, [data]);
  return ref;
}

const ListboxComponent = React.forwardRef(function ListboxComponent(props, ref) {
  const { children, ...other } = props;
  const itemData = React.Children.toArray(children);
  const theme = useTheme();
  const smUp = useMediaQuery(theme.breakpoints.up('sm'), { noSsr: true });
  const itemCount = itemData.length;
  const itemSize = smUp ? 36 : 48;

  const getChildSize = (child) => {
    if (React.isValidElement(child) && child.type === ListSubheader) {
      return 48;
    }

    return itemSize;
  };

  const getHeight = () => {
    if (itemCount > 8) {
      return 8 * itemSize;
    }
    return itemData.map(getChildSize).reduce((a, b) => a + b, 0);
  };

  const gridRef = useResetCache(itemCount);

  return (
    <div ref={ref}>
      <OuterElementContext.Provider value={other}>
        <VariableSizeList
          itemData={itemData}
          height={getHeight() + 2 * LISTBOX_PADDING}
          width="100%"
          ref={gridRef}
          outerElementType={OuterElementType}
          innerElementType="ul"
          itemSize={(index) => getChildSize(itemData[index])}
          overscanCount={5}
          itemCount={itemCount}
        >
          {renderRow}
        </VariableSizeList>
      </OuterElementContext.Provider>
    </div>
  );
});

ListboxComponent.propTypes = {
  children: PropTypes.node,
};

export const AddLocationModal = ({ broker, locations, refetch, intl }) => {
  const [iataCode, setIataCode] = useState(null);
  const [reqErr, setReqErr] = useState(null);

  useEffect(() => refetch, []);

  const [postLocationReq, doPost] = AxioHook(postLocation(), { manual: true })
  const [getClientsReq] = AxioHook(getAllLocation())

  let Body = null
  if (getClientsReq.loading) {
    Body = <h2>Loading...</h2>
  } else if (getClientsReq.data) {
    Body = (
      <>
        <Col span={24}>
          <lab.Autocomplete
            id="combo-box-demo"
            ListboxComponent={ListboxComponent}
            options={locations || []}
            onChange={(e, value) => {
              setIataCode(value)
            }}
            options={getClientsReq.data}
            getOptionLabel={(option) => `${option.iataCode} - (${option.airportName})`}
            renderInput={(params) => <TextField {...params} variant="outlined" label="Search Location" />}
          />
        </Col>
        <Col size={2}>
          <Button onClick={() => {
            doPost({ data: { id: iataCode.id, brokerId: broker.id } })
              .then(() => {
                refetch()
              })
          }}>{postLocationReq.loading ? 'Saving...' : 'Add Location'}</Button>
        </Col>
      </>
    );
  } else if (getClientsReq.error) {
    Body = <h2>Error fetching the resource</h2>
  }

  return (
    <Container fluid>
      <Row>
        {postLocationReq.error && (
          <Col>
            Error assigning the code
          </Col>
        )}
      </Row>

      <Row>
        {Body}
      </Row>
    </Container>
  );
}


export const AddLocation = injectIntl(
  connect(
    null,
  )(AddLocationModal)
);