import React, { useState, useEffect } from 'react';
import { TextField, Grid, Paper, FormControlLabel, Checkbox, Typography, Select, MenuItem } from '@material-ui/core';
import { getBranches } from "../../crud/client.crud";
import CircularProgress from '@material-ui/core/CircularProgress';
import EditIcon from '@material-ui/icons/Edit';
import DataTable from 'react-data-table-component';
import { CreateBranchModal } from "./CreateBranchModal";
import AxioHook from 'axios-hooks'
import {
    Button,
    Modal,
} from "react-bootstrap";


export const BranchListModal = ({ handleClose, client }) => {
    const [createNew, setCreateNew] = useState(false);
    const [selectedBranchId, setSelectedBranchId] = useState(false);
    const [branchesReq, doGet] = AxioHook(getBranches(client.id), { manual: true })

    const columns = [
        { name: 'Branch Name', selector: 'branchname'},
        { name: 'Email', selector: 'email'},
    ];

    useEffect(() => {
        doGet();
    }, []);

    let Body = null;

    if (branchesReq.loading) {
        Body = (<CircularProgress />)
    } else if (branchesReq.data) {
        Body = (
            <DataTable
                customStyles={{
                    rows: {
                        style: {
                          cursor: 'pointer', // override the row height
                        }
                      }
                }}
                noHeader={true}
                highlightOnHover={true}
                onRowClicked={(row) => setSelectedBranchId(row.id)}
                columns={columns}
                data={branchesReq.data}
            />
        );
    } else if (branchesReq.error) {
        Body = (
            <Typography>
                Error fetching the resource
            </Typography>
        );
    }

    return (
        <Modal size="lg" show={true} onHide={() => handleClose('hide')}>
            <Modal.Header closeButton>
                <Modal.Title>{`Branches for ${client.clientname}`}</Modal.Title>
            </Modal.Header>

            <Modal.Body>
                {Body}
            </Modal.Body>

            <Modal.Footer>
                <Button variant="secondary" onClick={() => handleClose('close')}>
                    Close
                  </Button>
                  <Button onClick={() => setCreateNew(true)}>
                    Create
                  </Button>
            </Modal.Footer>
            {createNew && <CreateBranchModal
                client={client}
                refetch={doGet}
                handleClose={() => setCreateNew(false)}
            />}
            {selectedBranchId && <CreateBranchModal
                client={client}
                branch={branchesReq.data.find(b => b.id === selectedBranchId)}
                refetch={doGet}
                handleClose={() => setSelectedBranchId(false)}
            />}
        </Modal>
    );
}
