import React, { useState, useEffect } from 'react';
import { TextField, FormControlLabel, Checkbox, Button } from '@material-ui/core';
import { updateClient } from "../../crud/client.crud";
import { CountryDropdown } from "../../widgets/countryDropdown/CountryDropdown";
import { Formik } from "formik";
import { injectIntl } from "react-intl";
import clsx from "clsx";
import AxioHook from 'axios-hooks'
import { connect } from "react-redux";
import { Modal } from "react-bootstrap";
import { useAppState } from '../AppState';

const CreateClientModalComponent = ({ handleClose, client, refetch, intl }) => {
  const [loadingButtonStyle, setLoadingButtonStyle] = useState({
    paddingRight: "2.5rem"
  });

  const [, setError] = useAppState('error');

  useEffect(() => refetch, []);

  const [updateReq, doUpdate] = AxioHook(updateClient(), { manual: true })

  return (
    <Modal size="lg" show={true} onHide={() => handleClose('hide')}>
      <Modal.Header closeButton>
        <Modal.Title>{client ? `Edit${client.name}` : "Create Client"}</Modal.Title>
      </Modal.Header>
      <Formik
        initialValues={client || {
          email: '',
          clientname: '',
          address: '',
          country: '',
          clickThroughUrl: '',
          affiliateCode: '',
          logo: null,
        }}
        validate={values => {
          const errors = {};

          if (!values.email) {
            errors.email = intl.formatMessage({
              id: "AUTH.VALIDATION.REQUIRED_FIELD"
            });
          }

          if (!values.clientname) errors.clientname = intl.formatMessage({
            id: "AUTH.VALIDATION.REQUIRED_FIELD"
          });

          if (!values.address) errors.address = intl.formatMessage({
            id: "AUTH.VALIDATION.REQUIRED_FIELD"
          });

          if (!values.country) errors.country = intl.formatMessage({
            id: "AUTH.VALIDATION.REQUIRED_FIELD"
          });

          return errors;
        }}
        onSubmit={(values, { setStatus, setSubmitting }) => {
          doUpdate({ data: values })
            .then(() => {
              setSubmitting(false)
              handleClose();
            })
            .catch(err => {
              setSubmitting(false);
              err.response && setError(err.response.data.error)
              err.response && setStatus(err.response.data.error)

            })
        }}
      >
        {({
          values,
          status,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue,
        }) => {
          console.log(status)
          return (
            <>
              <form
                noValidate={true}
                autoComplete="off"
                className="kt-form"
                onSubmit={handleSubmit}
              >
                <Modal.Body>
                  {status ? (
                    <div role="alert" className="alert alert-danger">
                      <div className="alert-text">{status}</div>
                    </div>
                  ) : null}

                  <div className="form-group">
                    <TextField
                      type="email"
                      label="Email"
                      margin="normal"
                      className="kt-width-full"
                      name="email"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.email}
                      helperText={touched.email && errors.email}
                      error={Boolean(touched.email && errors.email)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Client Name"
                      className="kt-width-full"
                      name="clientname"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.clientname}
                      helperText={touched.clientname && errors.clientname}
                      error={Boolean(touched.clientname && errors.clientname)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Click Through Url"
                      className="kt-width-full"
                      name="clickThroughUrl"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.clickThroughUrl}
                      helperText={touched.clickThroughUrl && errors.clickThroughUrl}
                      error={Boolean(touched.clickThroughUrl && errors.clickThroughUrl)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Affiliate Code "
                      className="kt-width-full"
                      name="affiliateCode"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.affiliateCode}
                      helperText={touched.affiliateCode && errors.affiliateCode}
                      error={Boolean(touched.affiliateCode && errors.affiliateCode)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Adress"
                      className="kt-width-full"
                      name="address"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.address}
                      helperText={touched.address && errors.address}
                      error={Boolean(touched.address && errors.address)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="City"
                      className="kt-width-full"
                      name="city"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.city}
                      helperText={touched.city && errors.city}
                      error={Boolean(touched.city && errors.city)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="State"
                      className="kt-width-full"
                      name="state"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.state}
                      helperText={touched.state && errors.state}
                      error={Boolean(touched.state && errors.state)}
                    />
                  </div>

                  <div className="form-group">
                    <CountryDropdown
                      handleBlur={handleBlur}
                      handleChange={handleChange}
                      values={values}
                      touched={touched}
                      errors={errors}
                      setFieldValue={setFieldValue}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Phone Number"
                      className="kt-width-full"
                      name="phonenumber"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.phonenumber}
                      helperText={touched.phonenumber && errors.phonenumber}
                      error={Boolean(touched.phonenumber && errors.phonenumber)}
                    />
                  </div>

                  <div className="form-group">
                    <FormControlLabel
                      control={<Checkbox
                        name="disabled"
                        defaultChecked={values.disabled === true}
                        error={Boolean(touched.disabled && errors.disabled)}
                        onChange={() => setFieldValue('disabled', !values.disabled)} />}
                      label="Disabled"
                    />

                  </div>

                  <div className="kt-login__actions">

                  </div>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="secondary" onClick={() => handleClose('close')}>
                    Close
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    id="kt_login_signin_submit"
                    type="submit"
                    disabled={isSubmitting}
                    className={`btn btn-primary btn-elevate kt-login__btn-primary ${clsx(
                      {
                        "kt-spinner kt-spinner--right kt-spinner--md kt-spinner--light": isSubmitting
                      }
                    )}`}
                    style={loadingButtonStyle}
                  >
                    Submit
                  </Button>
                </Modal.Footer>
              </form>

            </>
          );
        }}
      </Formik>

    </Modal>
  );
}


export const CreateClientModal = injectIntl(
  connect(
    null,
  )(CreateClientModalComponent)
);