import axios from "axios";
import store from "../store/store";
import { actionTypes } from "../store/ducks/auth.duck";

const catchErr = (error) => {
  if (error.response) {
    // The request was made and the server responded with a status code
    // that falls out of the range of 2xx
    console.log(error.response);
    console.log(error.response.data);
    console.log(error.response.status);
    if (error.response.status === 401) {
      store.dispatch({ type: actionTypes.Logout });
    }
    console.log(error.response.headers);
  } else if (error.request) {
    // The request was made but no response was received
    // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
    // http.ClientRequest in node.js
    console.log(error.request);
  } else {
    // Something happened in setting up the request that triggered an Error
    console.log('Error', error.message);
  }
  console.log(error.config);
  throw error;
}

export function iatacodes() {
  return axios({
    url: `${process.env.REACT_APP_BACKEND_URL ?  process.env.REACT_APP_BACKEND_URL : window.location.origin}/getiatacodes`
  })
    .then(r => r.data)
    .catch(catchErr);
}

export function deleteIatacode(id) {
  return axios({
    method: 'DELETE',
    url: `${process.env.REACT_APP_BACKEND_URL || window.location.origin}/iatacodes`,
    data: { id },
  })
    .then(r => r.data)
    .catch(catchErr);
}

export function clientIatacodes(clientID) {
  return axios({
    url: `${process.env.REACT_APP_BACKEND_URL ?  process.env.REACT_APP_BACKEND_URL : window.location.origin}/clientiatacodes/`
  })
    .then(r => r.data)
    .catch(catchErr);
}


export function postCodes(valuesToSend) {
  return {
    method: 'POST',
    url: `${process.env.REACT_APP_BACKEND_URL || window.location.origin}/iatacodes`,
}
}
