import React, { useState, useEffect } from 'react';
import { TextField, FormControlLabel, Checkbox } from '@material-ui/core';
import { postBroker } from "../../crud/broker.crud";
import { Formik } from "formik";
import { FormattedMessage, injectIntl } from "react-intl";
import clsx from "clsx";
import AxioHook from 'axios-hooks'
import { connect } from "react-redux";
import {
  Button,
  Modal,
} from "react-bootstrap";
import { useAppState } from '../AppState';

const CreateBrokerModalComponent = ({ handleClose, broker, refetch, intl }) => {
  const [loadingButtonStyle, setLoadingButtonStyle] = useState({
    paddingRight: "2.5rem"
  });

  const [, setError] = useAppState('error');

  useEffect(() => refetch, []);

  const [updateReq, doUpdate] = AxioHook(postBroker(), { manual: true })

  return (
    <Modal size="lg" show={true} onHide={() => handleClose('hide')}>
      <Modal.Header closeButton>
        <Modal.Title>{broker ? `Edit${broker.name}` : "Broker Client"}</Modal.Title>
      </Modal.Header>
      <Formik
        initialValues={broker || {
          email: '',
          clientname: '',
          address: '',
          country: '',
          logo: null,
        }}
        validate={values => {
          const errors = {};

          if (!values.email) {
            errors.email = intl.formatMessage({
              id: "AUTH.VALIDATION.REQUIRED_FIELD"
            });
          }

          if (!values.clientname) errors.clientname = intl.formatMessage({
            id: "AUTH.VALIDATION.REQUIRED_FIELD"
          });

          if (!values.address) errors.address = intl.formatMessage({
            id: "AUTH.VALIDATION.REQUIRED_FIELD"
          });

          return errors;
        }}
        onSubmit={(values, { setStatus, setSubmitting }) => {
          doUpdate({ data: values })
            .then(() => {
              setSubmitting(false)
              handleClose();
            })
            .catch(err => {
              setSubmitting(false);
              err.response && setError(err.response.data.error)
              err.response && setStatus(err.response.data.error)

            })
        }}
      >
        {({
          values,
          status,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue,
        }) => {
          return (
            <>
              <form
                noValidate={true}
                autoComplete="off"
                className="kt-form"
                onSubmit={handleSubmit}
              >
                <Modal.Body>
                  {status ? (
                    <div role="alert" className="alert alert-danger">
                      <div className="alert-text">{status}</div>
                    </div>
                  ) : null}

                  <div className="form-group">
                    <TextField
                      type="email"
                      label="Email"
                      margin="normal"
                      className="kt-width-full"
                      name="email"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.email}
                      helperText={touched.email && errors.email}
                      error={Boolean(touched.email && errors.email)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Client Name"
                      className="kt-width-full"
                      name="clientname"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.clientname}
                      helperText={touched.clientname && errors.clientname}
                      error={Boolean(touched.clientname && errors.clientname)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Adress"
                      className="kt-width-full"
                      name="address"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.address}
                      helperText={touched.address && errors.address}
                      error={Boolean(touched.address && errors.address)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Phone Number"
                      className="kt-width-full"
                      name="phonenumber"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.phonenumber}
                      helperText={touched.phonenumber && errors.phonenumber}
                      error={Boolean(touched.phonenumber && errors.phonenumber)}
                    />
                  </div>

                  <div className="form-group">
                    <FormControlLabel
                      control={<Checkbox
                        name="disabled"
                        defaultChecked={values.disabled === true}
                        error={Boolean(touched.disabled && errors.disabled)}
                        onChange={() => setFieldValue('disabled', !values.disabled)} />}
                      label="Disabled"
                    />

                  </div>

                  <div className="kt-login__actions">

                  </div>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="secondary" onClick={() => handleClose('close')}>
                    Close
                  </Button>
                  <button
                    id="kt_login_signin_submit"
                    type="submit"
                    disabled={isSubmitting}
                    className={`btn btn-primary btn-elevate kt-login__btn-primary ${clsx(
                      {
                        "kt-spinner kt-spinner--right kt-spinner--md kt-spinner--light": isSubmitting
                      }
                    )}`}
                    style={loadingButtonStyle}
                  >
                    Submit
                  </button>
                </Modal.Footer>
              </form>

            </>
          );
        }}
      </Formik>

    </Modal>
  );
}


export const CreateBrokerModal = injectIntl(
  connect(
    null,
  )(CreateBrokerModalComponent)
);