import React, { useState, useEffect } from 'react';
import { TextField, FormControlLabel, Checkbox, Button } from '@material-ui/core';
import { postBranch } from "../../crud/client.crud";
import { connect } from "react-redux";
import { Formik } from "formik";
import { injectIntl } from "react-intl";
import { CountryDropdown } from "../../widgets/countryDropdown/CountryDropdown";
import clsx from "clsx";
import AxioHook from 'axios-hooks'
import { Modal } from "react-bootstrap";
import { useAppState } from '../AppState';

export const CreateBranchModalComponent = ({ handleClose, branch, refetch, client, intl }) => {
  const [saving, setSaving] = useState('false');
  const [, setError] = useAppState('error');
  const [loadingButtonStyle, setLoadingButtonStyle] = useState({
    paddingRight: "2.5rem"
  });

  useEffect(() => refetch, []);

  const [postReq, doPost] = AxioHook(postBranch(client.id), { manual: true })

  return (
    <Modal size="lg" show={true} onHide={() => handleClose('hide')}>
      <Modal.Header closeButton>
        <Modal.Title>{branch? `Edit${branch.name}`: "Create Branch"}</Modal.Title>
      </Modal.Header>
      <Formik
        initialValues={branch || {
          email: '',
          branchname: '',
          address: '',
          country: '',
        }}
        validate={values => {
          const errors = {};

          if (!values.email) {
            errors.email = intl.formatMessage({
              id: "AUTH.VALIDATION.REQUIRED_FIELD"
            });
          }

          if (!values.branchname) {
            errors.branchname = intl.formatMessage({
              id: "AUTH.VALIDATION.REQUIRED_FIELD"
            });
          }

          if (!values.address) errors.address = intl.formatMessage({
            id: "AUTH.VALIDATION.REQUIRED_FIELD"
          });
          
          if (!values.country) errors.country = intl.formatMessage({
            id: "AUTH.VALIDATION.REQUIRED_FIELD"
          });

          return errors;
        }}
        onSubmit={(values, { setStatus, setSubmitting }) => {
          setSaving(true);
          doPost({ data: values })
          .then(() => {
            handleClose();
            setSubmitting(false)
          })
            .catch(err => {
              setSubmitting(false);
              err.response && setError(err.response.data.error)
              err.response && setStatus(err.response.data.error)
            })
        }}
      >
        {({
          values,
          status,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue
        }) => {
          console.log(status)
          return (
            <>
              <form
                noValidate={true}
                autoComplete="off"
                className="kt-form"
                onSubmit={handleSubmit}
              >
                <Modal.Body>
                {status && (
                  <div role="alert" className="alert alert-danger">
                    <div className="alert-text">{status}</div>
                  </div>
                )}

                  <div className="form-group">
                    <TextField
                      type="email"
                      label="Email"
                      margin="normal"
                      className="kt-width-full"
                      name="email"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.email}
                      helperText={touched.email && errors.email}
                      error={Boolean(touched.email && errors.email)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Branch Name"
                      className="kt-width-full"
                      name="branchname"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.branchname}
                      helperText={touched.branchname && errors.branchname}
                      error={Boolean(touched.branchname && errors.branchname)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Adress"
                      className="kt-width-full"
                      name="address"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.address}
                      helperText={touched.address && errors.address}
                      error={Boolean(touched.address && errors.address)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="City"
                      className="kt-width-full"
                      name="city"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.city}
                      helperText={touched.city && errors.city}
                      error={Boolean(touched.city && errors.city)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="State"
                      className="kt-width-full"
                      name="state"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.state}
                      helperText={touched.state && errors.state}
                      error={Boolean(touched.state && errors.state)}
                    />
                  </div>

                  <div className="form-group">
                    <CountryDropdown
                      handleBlur={handleBlur}
                      handleChange={handleChange}
                      values={values}
                      touched={touched}
                      errors={errors}
                      setFieldValue={setFieldValue}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Phone Number"
                      className="kt-width-full"
                      name="phonenumber"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.phonenumber}
                      helperText={touched.phonenumber && errors.phonenumber}
                      error={Boolean(touched.phonenumber && errors.phonenumber)}
                    />
                  </div>

                  <div className="form-group">
                    <FormControlLabel
                      control={<Checkbox
                        name="disabled"
                        defaultChecked={values.disabled === true}
                        error={Boolean(touched.disabled && errors.disabled)}
                        onChange={() => setFieldValue('disabled',!values.disabled)} />}
                      label="Disabled"
                    />

                  </div>

                  <div className="kt-login__actions">

                  </div>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="secondary" onClick={() => handleClose('close')}>
                    Close
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    id="kt_login_signin_submit"
                    type="submit"
                    disabled={isSubmitting}
                    className={`btn btn-primary btn-elevate kt-login__btn-primary ${clsx(
                      {
                        "kt-spinner kt-spinner--right kt-spinner--md kt-spinner--light": isSubmitting
                      }
                    )}`}
                    style={loadingButtonStyle}
                  >
                    Submit
                  </Button>
                </Modal.Footer>
              </form>

            </>
          )
        }}
      </Formik>

    </Modal>
  );
}


export const CreateBranchModal = injectIntl(
  connect(
    null,
  )(CreateBranchModalComponent)
);