import React, { useState, useEffect } from 'react';
import { Checkbox, Typography, Select, MenuItem } from '@material-ui/core';
import { unassignCode, getLocations } from "../../crud/broker.crud";
import { getLocations as getAllLocation } from "../../crud/locations.crud";
import DeleteIcon from '@material-ui/icons/Delete';
import DataTable from 'react-data-table-component';
import { AddLocationModal } from "./AddLocationModal";
import AxioHook from 'axios-hooks'
import { Button, Modal } from "react-bootstrap";


export const LocationListModal = ({ handleClose, broker, refetch }) => {
    const [createNew, setCreateNew] = useState(false);
    const [selectedBranchId, setSelectedBranchId] = useState(false);
    const [branchesReq, doGet] = AxioHook(getLocations(broker.id), { manual: true })
    const [unassignReq, doUnassign] = AxioHook(unassignCode(broker.id), { manual: true })

    const columns = [
        { name: 'IATA', selector: 'iataCode' },
        { name: 'Airport', selector: 'airportName' },
        { name: 'Location', selector: 'location' },
        { name: 'Country', selector: 'country' },
        {
            name: 'Delete',
            compact: true,
            maxWidth: "3%",
            minWidth: "70px",
            cell: (location) => {

                return (
                    <DeleteIcon
                        variant="warning"
                        onClick={() => {
                            doUnassign({ data: {id: location.id}})
                            .then(() => doGet())
                        }}
                    ></DeleteIcon>
                )
            }
        }
    ];

    useEffect(() => {
        doGet();
    }, []);

    let Body = <DataTable
        customStyles={{
            rows: {
                style: {
                    cursor: 'pointer', // override the row height
                }
            }
        }}
        progressPending={branchesReq.loading}
        noHeader={true}
        highlightOnHover={true}
        onRowClicked={(row) => setSelectedBranchId(row.id)}
        columns={columns}
        data={branchesReq.data}
    />;

    if (branchesReq.error) {
        Body = (
            <Typography>
                Error fetching the resource
            </Typography>
        );
    }

    return (
        <Modal size="lg" show={true} onHide={() => handleClose('hide')}>
            <Modal.Header closeButton>
                <Modal.Title>{`Locations for ${broker.clientname}`}</Modal.Title>
            </Modal.Header>

            <Modal.Body>
                <AddLocationModal
                    broker={broker}
                    refetch={doGet}
                />
                {Body}
            </Modal.Body>

            <Modal.Footer>
                <Button variant="secondary" onClick={() => handleClose('close')}>
                    Close
                  </Button>
            </Modal.Footer>
        </Modal>
    );
}
