import React, { Suspense, lazy } from "react";
import { connect } from "react-redux";
import { Redirect, Route, Switch } from "react-router-dom";
import Builder from "./Builder";
import Dashboard from "./Dashboard";
import LocationMap from "../config/LocationMap";
import Offering from "../offering/Offering";
import Config from "../config/Config";
import DocsPage from "./docs/DocsPage";
import { LayoutSplashScreen } from "../../../_metronic";
import Notification from "../../widgets/Notification";
import { useAppState } from '../AppState';
import Clients from "../clients";
import Profile from "../config/Profile";
import Paypal from "../config/Paypal";
import LocationPage from "../locations/index";
import BrokersPage from "../brokers/index";

const GoogleMaterialPage = lazy(() =>
  import("./google-material/GoogleMaterialPage")
);
const ReactBootstrapPage = lazy(() =>
  import("./react-bootstrap/ReactBootstrapPage")
);

function HomePage({ user, menuConfig }) {
  // useEffect(() => {
  //   console.log('Home page');
  // }, []) // [] - is required if you need only one call
  // https://reactjs.org/docs/hooks-reference.html#useeffect

  const [success, setSuccess] = useAppState('success');
  const [error, setError] = useAppState('error');

  const routes = [
    {path: "/builder" , component: Builder},
    {path: "/dashboard" , component: Dashboard},
    {path: "/docs" , component: DocsPage},
    {path: "/google-material", component: GoogleMaterialPage},
    {path: "/react-bootstrap", component: ReactBootstrapPage},
    {path: "/location-map", component: LocationMap},
    {path: "/scraper-config", component: Config},
    {path: "/offering" , component: Offering},
    {path: "/clients" , component: Clients},
    {path: "/edit-profile" , component: Profile},
    {path: "/payment" , component: Paypal},
    {path: "/locations" , component: LocationPage},
    {path: "/brokers" , component: BrokersPage},
  ]

  return (
    <Suspense fallback={<LayoutSplashScreen />}>
      {success && (
        <Notification msg={success} onClose={() => setSuccess(false)} />
      )}
      {error && (
        <Notification msg={error} onClose={() => setError(false)} severity={'error'} />
      )}
      <Switch>
        {
          /* Redirect from root URL to /dashboard. */
          <Redirect exact from="/" to="/dashboard" />
        }
        {
          routes
          .filter((r) => {
            const currentRoute = menuConfig.header.items.find(i => i.page === r.path.replace(/\//g, ''))
            if (!user) return true;
            if (!currentRoute) return true;
            if (!currentRoute.permissions) return true;
            if (currentRoute.permissions.length === 0) return true;

            return currentRoute.permissions.every(r => user.permissions.includes(r)) 
          })
          .filter((r) => {
            const currentRoute = menuConfig.aside.items.find(i => i.page === r.path.replace(/\//g, ''))
            if (!user) return true;
            if (!currentRoute) return true;
            if (!currentRoute.permissions) return true;
            if (currentRoute.permissions.length === 0) return true;

            return currentRoute.permissions.every(r => user.permissions.includes(r))
          })
          .map(r => <Route key={r.path} path={r.path} component={r.component} />)}        
        <Redirect to="/error/error-v1" />
      </Switch>
    </Suspense>
  );
}


const mapStateToProps = ({ auth: { user }, builder }) => ({
  user,
  menuConfig: builder.menuConfig,
});

export default connect(mapStateToProps)(HomePage);