import React, { useState } from "react";
import {
    Box, Typography, AppBar, Tabs, Tab, Fab, ExpansionPanel,
    ExpansionPanelSummary, ExpansionPanelActions,
    Divider, Button,
    ExpansionPanelDetails,
} from '@material-ui/core';
import AxioHook from 'axios-hooks'
import { postValues, deleteCategory } from "../../crud/categories.crud";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { CategoryValueForm } from "./CategoryValueForm";
import { createGlobalState } from 'react-hooks-global-state';
import { useAppState } from '../AppState';

const initialState = { inputCounter: [0], categoryValuesToSend: [] };
export const { useGlobalState: useCategoryValues, setGlobalState } = createGlobalState(initialState);
const resetState = () => {
    setGlobalState('inputCounter', initialState.inputCounter);
    setGlobalState('categoryValuesToSend', initialState.categoryValuesToSend);
}


const useStyles3 = makeStyles(theme => ({
    root: {
        width: "100%"
    },
    heading: {
        fontSize: theme.typography.pxToRem(15)
    },
    secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
        color: theme.palette.text.secondary
    },
    icon: {
        verticalAlign: "bottom",
        height: 20,
        width: 20
    },
    details: {
        alignItems: "center"
    },
    column: {
        flexBasis: "33.33%"
    },
    footer: {
        justifyContent: "space-between"
    },
    fullColumn: {
        flexBasis: "100%"
    },
    helper: {
        borderLeft: `2px solid ${theme.palette.divider}`,
        padding: theme.spacing(1, 2)
    },
    link: {
        color: theme.palette.primary.main,
        textDecoration: "none",
        "&:hover": {
            textDecoration: "underline"
        }
    }
}));


export function CategoryPanel({ category, refetch }) {
    const [inputCounter, setInputCounter] = useState([0]);
    const [valuesToSend, setValuesToSend] = useState([]);
    const classes3 = useStyles3();
    const [, setSuccess] = useAppState('success');

    const [getCategoriesReq, doPost] = AxioHook(postValues(category.id), { manual: true })
    const [deleteCategoryReq, doDeleteCategory] = AxioHook(deleteCategory(), { manual: true })

    const disableButtons = getCategoriesReq.loading || deleteCategoryReq.loading

    useState(() => resetState, [])

    return (
        <>
            <ExpansionPanel defaultExpanded>
                <ExpansionPanelSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1c-content"
                    id="panel1c-header"
                >
                    <div className={classes3.column}>
                        <Typography className={classes3.heading}>
                            {category.name.charAt(0).toUpperCase() + category.name.slice(1)}
                        </Typography>
                    </div>
                    <div className={classes3.column}>
                        <Typography className={classes3.secondaryHeading}>
                            Property to compare with: <code>{category.responseProperty}</code>
                        </Typography>
                    </div>
                    <div className={classes3.column}>
                        <Typography className={classes3.heading}>
                            Filter type: {category.type}
                        </Typography>
                    </div>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails className={classes3.details}>
                    <div className={classes3.fullColumn}>
                        {category.values.map((value) => {
                            return <CategoryValueForm onEdit={setValuesToSend} categoryType={category.type} onAdd={setInputCounter} key={value.id} {...value} />
                        })}
                        {inputCounter.map((id) => {
                            return <CategoryValueForm onEdit={setValuesToSend} categoryType={category.type} onAdd={setInputCounter} key={id} idx={id} />
                        })}
                    </div>
                </ExpansionPanelDetails>
                <Divider />
                <ExpansionPanelActions className={classes3.footer}>
                    <Button
                        size="small"
                        disabled={disableButtons}
                        color="secondary"
                        onClick={() => {
                            doDeleteCategory({ data: { id: category.id } })
                                .then(() => refetch())
                        }}
                    >Delete</Button>
                    <div>
                        {category.type === 'tag' && (
                            <Button size="small" disabled={disableButtons} color="primary" onClick={() => {
                                const toSend = valuesToSend.filter(v => v.name && v.value)
                                doPost({ data: { values: toSend } })
                                    .then(r => {
                                        setSuccess(r.data.success)
                                        refetch()
                                    });
                            }}>
                                {disableButtons ? "Saving..." : 'Save'}
                            </Button>
                        )}
                    </div>
                </ExpansionPanelActions>
            </ExpansionPanel>
        </>
    );
} 