import React, { useState, useEffect } from "react";
import { Box, Typography, AppBar, Tabs, Tab, Fab, ExpansionPanel } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import { CategoryPanel } from './CategoryPanel'
import { CreateCategoryModal } from './CreateCategoryModal'
import AxioHook from 'axios-hooks'
import { getCategories } from "../../crud/categories.crud";
import CircularProgress from '@material-ui/core/CircularProgress';


function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <Typography
            component="div"
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && <Box p={3}>{children}</Box>}
        </Typography>
    );
}

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

const fabStyle = {
    position: 'sticky',
    bottom: '10%',
    left: '100%'
};

const TabMap = {
    0: 'Cars',
    1: 'Hotels',
    2: 'Cruises',
    3: 'Flights',
}

export default function MyPage(p) {
    const [value, setValue] = useState(0);
    const [displayModal, setDisplayModal] = useState(false);

    const currentOffering = TabMap[value];

    const [getCategoriesReq, refetch] = AxioHook(getCategories(currentOffering))

    useEffect(() => {
        if (displayModal === false) refetch();
    }, [displayModal]);

    useEffect(() => {
        refetch();
    }, [value]);

    let Body = (<CircularProgress />)

    if (getCategoriesReq.loading) {
        Body = (<CircularProgress />)
    } else if (getCategoriesReq.data && getCategoriesReq.data.length !== 0) {
        Body = (
            <>
                {getCategoriesReq.data.map(c => {
                    return <CategoryPanel refetch={refetch} key={c.id} category={c} />
                })}
            </>
        );
    } else if (getCategoriesReq.data && getCategoriesReq.data.length === 0) {
        Body = (
            <h2>No categories found for {currentOffering}. Create one!</h2>
        );
    }
     else if (getCategoriesReq.error) {
        Body = (
            <Typography>
                Error fetching the resource
            </Typography>
        );
    }

    const handleTabChange = (event, newValue) => setValue(newValue)
    return (
        <>
            <AppBar position="static">
                <Tabs value={value} onChange={handleTabChange} aria-label="simple tabs example">
                    <Tab label="Cars" {...a11yProps(0)} />
                    <Tab label="Hotels" {...a11yProps(1)} />
                    <Tab label="Cruises" {...a11yProps(2)} />
                    <Tab label="Flights" {...a11yProps(3)} />
                </Tabs>
            </AppBar>
            <TabPanel value={value} index={0}>
                {Body}
            </TabPanel>
            <TabPanel value={value} index={1}>
                {Body}
            </TabPanel>
            <TabPanel value={value} index={2}>
                {Body}
            </TabPanel>
            <TabPanel value={value} index={3}>
                {Body}
            </TabPanel>

            <Fab color="primary" onClick={() => setDisplayModal(true)} aria-label="add" style={fabStyle}>
                <AddIcon />
            </Fab>

            {displayModal && <CreateCategoryModal offering={currentOffering} handleClose={() => setDisplayModal(false)} />}
        </>
    );
} 