import React, { useEffect, useState } from 'react';
import { PayPalButton } from "react-paypal-button-v2";
import { Container, Row, Col, Table } from 'react-bootstrap';
import { Typography } from '@material-ui/core';
import { getTransactions, postTransaction } from "../../crud/scraper.crud";
import Notification from "../../widgets/Notification";

const FormCreate = () => {
    const [payments, setPayments] = useState([])
    const [success, setSuccess] = useState(false);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        getTransactions().then(r => setPayments(r))
    }, []);

    return (
        <Container>
            {success && (
                <Notification msg={success} onClose={() => setSuccess(false)} />
            )}
            <Row>
                <Col offset={6} span={12} >
                    {loading && (
                        <Typography variant="h3" gutterBottom>
                            Loading Paypal payment...
                        </Typography>
                    ) }
                    <PayPalButton
                            onButtonReady={() => {
                                console.log('conde')
                                setLoading(false)
                            }}
                            options={{
                                clientId: 'AX1dp6TsC0lUgPd-N_ezG8VggCXXQruDTnd8b55ZpoEFYOz5KSX3lpyOeCdAhIdBdohIyRb0mLxAxqtg'
                            }}
                            amount="0.01"
                            onSuccess={(details, data) => {
                                postTransaction()
                                    .then(() => {
                                        setSuccess("Transaction completed by " + details.payer.name.given_name);
                                    });
                            }}
                        />
                </Col>
            </Row>
            <Row>
                <Col span={24}>
                    <Table >
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>Order ID</th>
                                <th>Created At</th>
                            </tr>
                        </thead>
                        <tbody>
                            {payments.map((p, idx) => {
                                console.log(p)
                                return (
                                    <tr>
                                        <td>{idx}</td>
                                        <td>{p.id}</td>
                                        <td>{p.id}</td>
                                    </tr>
                                );
                            })}
                        </tbody>
                    </Table>
                </Col>
            </Row>
        </Container>
    );
}

export default FormCreate;
