import React, { useState, useEffect } from 'react';
import { TextField, CircularProgress } from '@material-ui/core';
import clsx from "clsx";
import { Formik } from "formik";
import { getUserByToken } from "../../crud/auth.crud";
import { putClient } from "../../crud/client.crud";
import { getFields, postFields } from "../../crud/scraper.crud";
import Notification from "../../widgets/Notification";
import AxioHook from 'axios-hooks'

const FormCreate = () => {
    const [loadingButtonStyle] = useState({
        paddingRight: "2.5rem"
    });
    const [user, setUser] = useState(null);

    const [{ data: fields, loading, error }, refetch] = AxioHook(getFields())
    const [{ data: postedData, loading: postingFields, error: errorPost }, postFieldData] = AxioHook(postFields(), { manual: true })
    const [{ data: clientData, loading: puttingClient, error: errorPuttingClient }, putClientReq] = AxioHook(putClient(), { manual: true })

    useEffect(() => {
        getUserByToken()
        .then(r => setUser(r.data));
    }, []);

    const FormField = (fieldProps) => {
        return (
            <div className="form-group">
                <TextField
                    margin="normal"
                    label={fieldProps.label}
                    className="kt-width-full"
                    name={fieldProps.name}
                    placeholder={`path.to.${fieldProps.placeholder}`}
                    onBlur={fieldProps.handleBlur}
                    onChange={fieldProps.handleChange}
                    value={fieldProps.values[fieldProps.name]}
                />
            </div>
        );
    }

    if (error) {
        // TODO: center messsage and extract as a widget
        return (<>
            <h3>{error.toString()}</h3>
            <button onClick={refetch}>Try again!</button>
        </>);
    }

    if (loading) {
        return (<>
            <h3>Fetching Data</h3>
            <CircularProgress />
        </>);
    }

    return (
        <>
            {((!postingFields && postedData) || (!puttingClient && clientData)) && (
                <Notification msg="Config updated!" />
            )}
            {((!postingFields && errorPost) || (!puttingClient && errorPuttingClient)) && (
                <Notification msg="Could not update config!" severity={'error'} />
            )}
            <Formik
                enableReinitialize={true}
                initialValues={fields.filter(f => "property" in f).reduce((prev, next) => {
                    prev[next.property] = next.path 
                    return prev;
                }, { urlToScrap: user ? user.urlToScrap : '' })}
                onSubmit={(values, { setStatus, setSubmitting }) => {
                    let promise = Promise.resolve();
                    if (values.urlToScrap) {
                        promise = putClientReq({ data: { urlToScrap: values.urlToScrap } })
                    }

                    promise
                        .then(() => {
                            delete values.urlToScrap;
                            return postFieldData({ data: { fields: values } })
                        })
                        .then(() => setSubmitting(false))
                        .then(() => getFields())
                        .catch(() => setSubmitting(false))

                }}
            >
                {(formProp) => (
                    <form
                        noValidate={true}
                        autoComplete="off"
                        className="kt-form"
                        onSubmit={formProp.handleSubmit}
                    >
                        {formProp.status ? (
                            <div role="alert" className="alert alert-danger">
                                <div className="alert-text">{formProp.status}</div>
                            </div>
                        ) : null}

                        <TextField
                            margin="normal"
                            label={`URL to scrap`}
                            className="kt-width-full"
                            name={'urlToScrap'}
                            placeholder={formProp.placeholder}
                            onBlur={formProp.handleBlur}
                            onChange={formProp.handleChange}
                            variant="outlined"
                            helperText="URL we will fetch to get your data"
                            value={formProp.values.urlToScrap}
                        />

                        {fields.filter(f => "property" in f).map(f => {
                            return (
                                <FormField key={f.property} {...formProp} value={formProp.values[f.property]} name={f.property} placeholder={`path.to.${f.property}`} label={f.property} handleChange={formProp.handleChange} onBlur={formProp.handleBlur} />
                            )
                        })}

                        {fields.filter(f => "name" in f).map(f => (
                            <FormField key={f.name} {...formProp} value={formProp.values[f.name]} name={f.name} placeholder={`path.to.${f.name}`} label={f.name} handleChange={formProp.handleChange} onBlur={formProp.handleBlur} />
                        ))}


                        <div className="kt-login__actions">

                            <button
                                id="kt_login_signin_submit"
                                type="submit"
                                disabled={formProp.isSubmitting}
                                className={`btn btn-primary btn-elevate kt-login__btn-primary ${clsx(
                                    {
                                        "kt-spinner kt-spinner--right kt-spinner--md kt-spinner--light": postingFields
                                    }
                                )}`}
                                style={loadingButtonStyle}
                            >
                                Save
                            </button>
                        </div>
                    </form>
                )}
            </Formik>
        </>
    );
}

export default FormCreate;