import React, { useState, useEffect } from "react";
import { CircularProgress, Input, Button } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import AxioHook from 'axios-hooks'
import EditIcon from '@material-ui/icons/Edit';
import ListIcon from '@material-ui/icons/List';
import DataTable from 'react-data-table-component';
import { Container, Row, Col, Table } from 'react-bootstrap';
import { getLocations, postLocation } from "../../crud/locations.crud";
import { CreateLocationModal } from "./CreateLocationModal";
import FuzzySearch from 'fuzzy-search';

const TableIconsStyles = {
    cursor: 'pointer'
};

export default function Clients() {
    const [searcher, setSearcher] = useState(null);
    const [dataToDisplay, setDataToDisplay] = useState([]);
    const [displayModal, setDisplayModal] = useState(false);
    const [loadingToggle, setLoadingToggle] = useState(false);
    const [getClientsReq, refetch] = AxioHook(getLocations())
    const [displayUpdateModal, setDisplayUpdateModal] = useState(false);
    const [displayBranchModal, setDisplayBranchModal] = useState(false);
    const [updateReq, doUpdate] = AxioHook(postLocation(), { manual: true })

    useEffect(() => {
        if (getClientsReq.data) {
            setDataToDisplay(getClientsReq.data)
            if (!searcher) setSearcher(new FuzzySearch(getClientsReq.data, ['iataCode', 'airportName', "location", "country"], {}))
        }
    }, [getClientsReq]);

    const columns = [
        {
            name: 'IATA',
            selector: 'iataCode',
            maxWidth: "3%",
            minWidth: "60px",
        },
        {
            name: 'Airport Name',
            compact: true,
            selector: 'airportName',
            maxWidth: "30%",
        },
        {
            name: 'Location',
            compact: true,
            selector: 'location',
            maxWidth: "20%",
        },
        {
            name: 'Country',
            compact: true,
            selector: 'country',
            maxWidth: "10%",
            minWidth: "90px",
        },
        {
            name: 'Disabled?',
            compact: true,
            selector: 'disabled',
            maxWidth: "3%",
            minWidth: "70px",
            cell: (r) => r.disabled ? "yes" : "no"
        },
        {
            name: 'Edit',
            compact: true,
            maxWidth: "3%",
            minWidth: "70px",
            style: TableIconsStyles,
            cell: (r) => <EditIcon onClick={() => setDisplayUpdateModal(r)}></EditIcon>,
        },
        {
            name: 'Branches',
            compact: true,
            maxWidth: "3%",
            minWidth: "70px",
            style: TableIconsStyles,
            cell: (r) => <ListIcon variant="info" onClick={() => setDisplayBranchModal(r)}></ListIcon>,
        },
        {
            name: 'Disable',
            compact: true,
            maxWidth: "3%",
            minWidth: "70px",
            style: TableIconsStyles,
            cell: (client) => {
                const disabldBtnTxt = client.disabled ? "Enable" : "Disable"

                return (
                    <DeleteIcon
                        variant="warning"
                        disbled={updateReq.loading}
                        onClick={() => {
                            setLoadingToggle(client)
                            doUpdate({ data: { id: client.id, disabled: !client.disabled } })
                                .then(() => {
                                    return refetch()
                                })
                                .then(() => setLoadingToggle(false))
                        }}
                    >{loadingToggle.id === client.id ? "Wait..." : disabldBtnTxt}</DeleteIcon>
                )
            }
        },
    ];


    let Body = null

    if (getClientsReq.loading) {
        Body = (<>
            <CircularProgress />
        </>)
    } else if (dataToDisplay) {
        Body = (
            <Col>
                <DataTable
                    subHeaderAlign="center"
                    title="Locations"
                    actions={
                        <>
                            <Input type="text" onChange={(e) => {
                                if (searcher) setDataToDisplay(searcher.search(e.target.value))
                            }} />
                            <Button variant="contained" color="primary" onClick={() => setDisplayModal(true)}>Add Location</Button>
                        </>
                    }
                    columns={columns}
                    pagination={true}
                    data={dataToDisplay}
                />
            </Col>
        );
    } 
    if (getClientsReq.error) {
        Body = (
            <>
                <h3>Error fetching the resource</h3>
            </>
        );
    }

    return (
        <Container fluid>
            <Row>
                {Body}
            </Row>
            {displayModal && <CreateLocationModal refetch={refetch} handleClose={() => setDisplayModal(false)} />}
            {displayUpdateModal && <CreateLocationModal iataCode={displayUpdateModal} refetch={refetch} handleClose={() => setDisplayUpdateModal(false)} />}
        </Container>
    );
}
