import React, { useEffect, useState } from 'react';
import { Formik } from "formik";
import { connect } from "react-redux";
import { Button, TextField, FormControlLabel, Checkbox } from '@material-ui/core';
import { getUserByTokenForHook } from "../../crud/auth.crud";
import { putClient } from "../../crud/client.crud";
import clsx from "clsx";
import AxioHook from 'axios-hooks'
import { useAppState } from '../AppState';

const Profile = (props) => {
    const [, setError] = useAppState('error');
    const [loadingButtonStyle] = useState({
        paddingRight: "2.5rem"
    });

    const [userReq, doGetUser] = AxioHook(getUserByTokenForHook())
    const [updateReq, doUpdate] = AxioHook(putClient(), { manual: true })

    if (userReq.loading) {
        return <h2>Fetching Profile...</h2>
    }

    if (userReq.error) {
        return <h2>Error fetching profile</h2>
    }

    return (
        <Formik
            enableReinitialize={true}
            initialValues={userReq.data || {
                email: '',
                clientname: '',
                address: '',
                country: '',
                logo: null,
            }}
            onSubmit={(values, { setStatus, setSubmitting }) => {
                const formData = new FormData();
                delete values.logo
                Object.keys(values).filter(key => values[key] !== null && values[key] !== undefined).map(key => {
                    formData.append(key, values[key])
                })

                doUpdate({
                    data: formData,
                    config: { headers: {'Content-Type': 'multipart/form-data' }}
                })
                    .then(() => {
                        doGetUser()
                        setSubmitting(false)
                    })
                    .catch(err => {
                        setSubmitting(false);
                        err.response && setError(err.data)
                        err.response && setStatus(err.data)
                    })
            }}
        >
            {({
                values,
                status,
                errors,
                touched,
                handleChange,
                handleBlur,
                handleSubmit,
                isSubmitting,
                setFieldValue,
            }) => (
                    <form
                        noValidate={true}
                        autoComplete="off"
                        className="kt-form"
                        onSubmit={handleSubmit}
                    >
                        {status ? (
                            <div role="alert" className="alert alert-danger">
                                <div className="alert-text">{status}</div>
                            </div>
                        ) : null}

                        <TextField
                            margin="normal"
                            label={`URL to scrap`}
                            className="kt-width-full"
                            name={'urlToScrap'}
                            placeholder={'Url to scrap'}
                            onBlur={handleBlur}
                            onChange={handleChange}
                            variant="outlined"
                            helperText="URL we will fetch to get your data"
                            value={values.urlToScrap}
                        />

                        <div className="form-group">
                            <TextField
                                type="email"
                                label="Email"
                                margin="normal"
                                className="kt-width-full"
                                name="email"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                value={values.email}
                                helperText={touched.email && errors.email}
                                error={Boolean(touched.email && errors.email)}
                            />
                        </div>

                        <div className="form-group">
                            <TextField
                                type="string"
                                margin="normal"
                                label="Client Name"
                                className="kt-width-full"
                                name="clientname"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                value={values.clientname}
                                helperText={touched.clientname && errors.clientname}
                                error={Boolean(touched.clientname && errors.clientname)}
                            />
                        </div>

                        <div className="form-group">
                            <TextField
                                type="string"
                                margin="normal"
                                label="Adress"
                                className="kt-width-full"
                                name="address"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                value={values.address}
                                helperText={touched.address && errors.address}
                                error={Boolean(touched.address && errors.address)}
                            />
                        </div>

                        <div className="form-group">
                            <TextField
                                type="string"
                                margin="normal"
                                label="City"
                                className="kt-width-full"
                                name="city"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                value={values.city}
                                helperText={touched.city && errors.city}
                                error={Boolean(touched.city && errors.city)}
                            />
                        </div>

                        <div className="form-group">
                            <TextField
                                type="string"
                                margin="normal"
                                label="State"
                                className="kt-width-full"
                                name="state"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                value={values.state}
                                helperText={touched.state && errors.state}
                                error={Boolean(touched.state && errors.state)}
                            />
                        </div>

                        <div className="form-group">
                            <TextField
                                type="string"
                                margin="normal"
                                label="Country"
                                className="kt-width-full"
                                name="country"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                value={values.country}
                                helperText={touched.country && errors.country}
                                error={Boolean(touched.country && errors.country)}
                            />
                        </div>

                        <div className="form-group">
                            <TextField
                                type="string"
                                margin="normal"
                                label="Phone Number"
                                className="kt-width-full"
                                name="phonenumber"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                value={values.phonenumber}
                                helperText={touched.phonenumber && errors.phonenumber}
                                error={Boolean(touched.phonenumber && errors.phonenumber)}
                            />
                        </div>

                        <div className="form-group">
                            <input
                                accept="image/*"
                                id="contained-button-file"
                                name="logo"
                                type="file"
                                onChange={(event) => setFieldValue("logo", event.currentTarget.files[0])}
                                style={{ display: 'none' }}
                            />
                            <label htmlFor="contained-button-file">
                                <Button variant="contained" color="primary" component="span">
                                    Upload
                            </Button>
                            </label>
                            {values.logo_name && (
                                <div className="kt-widget4__item ">
                                    <div className="kt-widget4__pic kt-widget4__pic--pic ">
                                        <img alt="" src={`${process.env.PUBLIC_URL}upload/${values.logo_name}`} />
                                    </div>
                                </div>
                            )}
                        </div>


                        <div className="kt-login__actions">

                            <button
                                id="kt_login_signin_submit"
                                type="submit"
                                disabled={isSubmitting}
                                className={`btn btn-primary btn-elevate kt-login__btn-primary ${clsx(
                                    {
                                        "kt-spinner kt-spinner--right kt-spinner--md kt-spinner--light": false
                                    }
                                )}`}
                                style={loadingButtonStyle}
                            >
                                Save
                            </button>
                        </div>
                    </form>
                )}
        </Formik>
    );
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default connect(mapStateToProps)(Profile);
