import React, { useState, useEffect } from 'react';
import { TextField, FormControlLabel, Checkbox, Button } from '@material-ui/core';
import { postLocation } from "../../crud/locations.crud";
import { Formik } from "formik";
import { FormattedMessage, injectIntl } from "react-intl";
import clsx from "clsx";
import AxioHook from 'axios-hooks'
import { connect } from "react-redux";
import { Modal } from "react-bootstrap";
import { useAppState } from '../AppState';

const CreateLocationComponent = ({ handleClose, iataCode, refetch, intl, broker }) => {
  const [loadingButtonStyle, setLoadingButtonStyle] = useState({
    paddingRight: "2.5rem"
  });

  const [, setError] = useAppState('error');

  useEffect(() => refetch, []);

  const [updateReq, doUpdate] = AxioHook(postLocation(), { manual: true })

  return (
    <Modal size="lg" show={true} onHide={() => handleClose('hide')}>
      <Modal.Header closeButton>
        <Modal.Title>{iataCode ? `Edit ${iataCode.iataCode}` : "Create Location"}</Modal.Title>
      </Modal.Header>
      <Formik
        initialValues={iataCode || {
          iataCode: '',
          airportName: '',
          location: '',
          country: '',
        }}
        validate={values => {
          const errors = {};

          if (!values.iataCode) {
            errors.iataCode = intl.formatMessage({
              id: "AUTH.VALIDATION.REQUIRED_FIELD"
            });
          }

          if (!values.airportName) errors.airportName = intl.formatMessage({
            id: "AUTH.VALIDATION.REQUIRED_FIELD"
          });

          if (!values.location) errors.location = intl.formatMessage({
            id: "AUTH.VALIDATION.REQUIRED_FIELD"
          });

          if (!values.country) errors.country = intl.formatMessage({
            id: "AUTH.VALIDATION.REQUIRED_FIELD"
          });

          return errors;
        }}
        onSubmit={(values, { setStatus, setSubmitting }) => {
          const body = values
          if (broker) body.brokerId = broker.id
          doUpdate({ data: body })
            .then(() => {
              setSubmitting(false)
              handleClose();
            })
            .catch(err => {
              setSubmitting(false);
              err.response && setError(err.response.data.error)
              err.response && setStatus(err.response.data.error)

            })
        }}
      >
        {({
          values,
          status,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
          setFieldValue,
        }) => {
          console.log(status)
          return (
            <>
              <form
                noValidate={true}
                autoComplete="off"
                className="kt-form"
                onSubmit={handleSubmit}
              >
                <Modal.Body>
                  {status ? (
                    <div role="alert" className="alert alert-danger">
                      <div className="alert-text">{status}</div>
                    </div>
                  ) : null}

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="IATA Code"
                      className="kt-width-full"
                      name="iataCode"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.iataCode}
                      helperText={touched.iataCode && errors.iataCode}
                      error={Boolean(touched.iataCode && errors.iataCode)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Airport Name"
                      className="kt-width-full"
                      name="airportName"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.airportName}
                      helperText={touched.airportName && errors.airportName}
                      error={Boolean(touched.airportName && errors.airportName)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Location"
                      className="kt-width-full"
                      name="location"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.location}
                      helperText={touched.location && errors.location}
                      error={Boolean(touched.location && errors.location)}
                    />
                  </div>

                  <div className="form-group">
                    <TextField
                      type="string"
                      margin="normal"
                      label="Country"
                      className="kt-width-full"
                      name="country"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.country}
                      helperText={touched.country && errors.country}
                      error={Boolean(touched.country && errors.country)}
                    />
                  </div>


                  <div className="form-group">
                    <FormControlLabel
                      control={<Checkbox
                        name="disabled"
                        defaultChecked={values.disabled === true}
                        error={Boolean(touched.disabled && errors.disabled)}
                        onChange={() => setFieldValue('disabled', !values.disabled)} />}
                      label="Disabled"
                    />

                  </div>

                  <div className="kt-login__actions">

                  </div>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="secondary" onClick={() => handleClose('close')}>
                    Close
                  </Button>
                  <Button
                    variant="contained"
                    color="primary"
                    id="kt_login_signin_submit"
                    type="submit"
                    disabled={isSubmitting}
                    className={`btn btn-primary btn-elevate kt-login__btn-primary ${clsx(
                      {
                        "kt-spinner kt-spinner--right kt-spinner--md kt-spinner--light": isSubmitting
                      }
                    )}`}
                    style={loadingButtonStyle}
                  >
                    Submit
                  </Button>
                </Modal.Footer>
              </form>

            </>
          );
        }}
      </Formik>

    </Modal>
  );
}


export const CreateLocationModal = injectIntl(
  connect(
    null,
  )(CreateLocationComponent)
);