import React, { useState, useEffect } from 'react';
import { TextField, Grid, Paper, FormControlLabel, Checkbox, Typography } from '@material-ui/core';
import { postCategories, getCategories, deleteCategory } from "../../crud/categories.crud";
import AxioHook from 'axios-hooks'
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import AddIcon from '@material-ui/icons/Add';
import CircularProgress from '@material-ui/core/CircularProgress';
import { useCategoryValues } from './CategoryPanel';

export const CategoryValueForm = ({ idx, id, name, value, disabled, categoryType, onEdit, onAdd }) => {
  const [, setInputCounter] = useCategoryValues('inputCounter');


  const defaultValues = { idx: idx, disabled: false };
  if (id) {
    defaultValues.id = id
    defaultValues.idx = id
  }
  if (name) defaultValues.name = name
  if (value) defaultValues.value = value
  if (disabled) defaultValues.disabled = disabled
  const [category, setCategory] = useState(defaultValues);

  const [deleteCategoryReq, doDeleteCategory] = AxioHook(deleteCategory(), { manual: true })

  useEffect(() => {
    onEdit(prev => {
      console.log(prev)
      const found = prev.find(p => p.idx === category.idx)
      if (found) return [...prev.filter(p => p.idx !== found.idx), category]

      prev.push(category)
      return prev;
    })
  }, [category])

  if (categoryType !== 'tag') return <></>

  return (
    <Paper style={{ marginBottom: '1rem', padding: '0.5rem' }}>
      <Grid container spacing={1} alignItems="center">

        <Grid item xs={3}>
          <TextField
            style={{ width: '100%' }}
            helperText={'Text to be display on filter menu'}
            defaultValue={name}
            onChange={(e) => {
              e.persist();
              setCategory(prev => ({ ...prev, name: e.target.value }))
            }} placeholder="Name" />
        </Grid>

        <Grid item xs={4}>
          <TextField
            style={{ width: '100%' }}
            helperText={'Value that will be match agains the property'}
            defaultValue={value}
            onChange={(e) => {
              e.persist();
              setCategory(prev => ({ ...prev, value: e.target.value }))
            }} placeholder="value" />
        </Grid>

        <Grid item style={{ cursor: 'pointer' }} >
          <FormControlLabel
            xs={2}
            control={<Checkbox
              defaultChecked={disabled}
              onChange={() => {
                setCategory(prev => ({ ...prev, disabled: !prev.disabled }))
              }} />}
            label="Disabled"
          />
        </Grid>

        <Grid item xs={2} style={{ cursor: 'pointer', display: 'flex', justifyContent: 'center' }}
          onClick={() => onAdd(prev => [...prev, prev[prev.length - 1] + 1])} >
          <AddIcon />
        </Grid>
        <Grid item
          xs={1}
          style={{ cursor: 'pointer', display: 'flex', justifyContent: 'center' }}
          onClick={async () => {
            if (id) await doDeleteCategory({ data: { id: id } });
            setInputCounter(prev => {
              const found = prev.find(v => v === idx);
              if (found !== undefined) return prev.filter(v => v !== found)
              return prev
            });
          }}
        >
          {deleteCategoryReq.loading ? <CircularProgress size={20} /> : <DeleteForeverIcon />}
        </Grid>
      </Grid>
    </Paper>
  );
}
