import React, { useState } from "react";
import { CircularProgress, Button } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import AxioHook from 'axios-hooks'
import EditIcon from '@material-ui/icons/Edit';
import ListIcon from '@material-ui/icons/List';
import DataTable from 'react-data-table-component';
import { Container, Row, Col, Table } from 'react-bootstrap';
import { getClients, updateClient } from "../../crud/client.crud";
import { CreateClientModal } from "./CreateClientModal";
import { BranchListModal } from "./BranchListModal";

const TableIconsStyles = {
    cursor: 'pointer'
};

export default function Clients() {
    const [displayModal, setDisplayModal] = useState(false);
    const [loadingToggle, setLoadingToggle] = useState(false);
    const [getClientsReq, refetch] = AxioHook(getClients())
    const [displayUpdateModal, setDisplayUpdateModal] = useState(false);
    const [displayBranchModal, setDisplayBranchModal] = useState(false);
    const [updateReq, doUpdate] = AxioHook(updateClient(), { manual: true })

    const columns = [
        {
            name: 'Client Name',
            selector: 'clientname',
        },
        {
            name: 'Email',
            selector: 'email',
        },
        {
            name: 'Phone number',
            selector: 'phonenumber',
        },
        {
            name: 'Disabled?',
            selector: 'disabled',
            maxWidth: "3%",
            minWidth: "70px",
            cell: (r) => r.disabled ? "yes" : "no"
        },
        {
            name: 'Edit',
            maxWidth: "3%",
            minWidth: "70px",
            style: TableIconsStyles,
            cell: (r) => <EditIcon onClick={() => setDisplayUpdateModal(r)}></EditIcon>,
        },
        {
            name: 'Branches',
            maxWidth: "3%",
            minWidth: "70px",
            style: TableIconsStyles,
            cell: (r) => <ListIcon variant="info" onClick={() => setDisplayBranchModal(r)}></ListIcon>,
        },
        {
            name: 'Disable',
            maxWidth: "3%",
            minWidth: "70px",
            style: TableIconsStyles,
            cell: (client) => {
                const disabldBtnTxt = client.disabled ? "Enable" : "Disable"

                return (
                    <DeleteIcon
                        variant="warning"
                        disbled={updateReq.loading}
                        onClick={() => {
                            setLoadingToggle(client)
                            doUpdate({ data: { id: client.id, disabled: !client.disabled } })
                                .then(() => {
                                    return refetch()
                                })
                                .then(() => setLoadingToggle(false))
                        }}
                    >{loadingToggle.id === client.id ? "Wait..." : disabldBtnTxt}</DeleteIcon>
                )
            }
        },
    ];


    let Body = null

    if (getClientsReq.loading) {
        Body = (<>
            <CircularProgress />
        </>)
    } else if (getClientsReq.data) {
        Body = (
            <Col span={24}>
                <DataTable
                subHeaderAlign="center"
                    title="Clients"
                    actions={<Button variant="contained" color="primary" onClick={() => setDisplayModal(true)}>Add Client</Button>}
                    columns={columns}
                    data={getClientsReq.data}
                />
            </Col>
        );
    } else if (getClientsReq.error) {
        Body = (
            <>
                <h3>Error fetching the resource</h3>
            </>
        );
    }

    return (
        <Container fluid>
            <Row>
                {Body}
            </Row>
            {displayBranchModal && <BranchListModal client={displayBranchModal} refetch={refetch} handleClose={() => setDisplayBranchModal(false)} />}
            {displayModal && <CreateClientModal refetch={refetch} handleClose={() => setDisplayModal(false)} />}
            {displayUpdateModal && <CreateClientModal client={displayUpdateModal} refetch={refetch} handleClose={() => setDisplayUpdateModal(false)} />}
        </Container>
    );
}
