import React, { useState } from "react";
import { CircularProgress, Button } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import AxioHook from 'axios-hooks'
import EditIcon from '@material-ui/icons/Edit';
import ListIcon from '@material-ui/icons/List';
import DataTable from 'react-data-table-component';
import { Container, Row, Col, Table } from 'react-bootstrap';
import { getBroker, postBroker } from "../../crud/broker.crud";
import { CreateBrokerModal } from "./CreateBrokerModal";
import { LocationListModal } from "./LocationsListModal";

const TableIconsStyles = {
    cursor: 'pointer'
};

export default function Brokers() {
    const [displayModal, setDisplayModal] = useState(false);
    const [loadingToggle, setLoadingToggle] = useState(false);
    const [getClientsReq, refetch] = AxioHook(getBroker())
    const [displayUpdateModal, setDisplayUpdateModal] = useState(false);
    const [displayBranchModal, setDisplayBranchModal] = useState(false);
    const [updateReq, doUpdate] = AxioHook(postBroker(), { manual: true })

    const columns = [
        {
            name: 'Broker Name',
            selector: 'clientname',
        },
        {
            name: 'Email',
            selector: 'email',
        },
        {
            name: 'Phone number',
            selector: 'phonenumber',
        },
        {
            name: 'Disabled?',
            selector: 'disabled',
            maxWidth: "3%",
            minWidth: "70px",
            cell: (r) => r.disabled ? "yes" : "no"
        },
        {
            name: 'Edit',
            maxWidth: "3%",
            minWidth: "70px",
            style: TableIconsStyles,
            cell: (r) => <EditIcon onClick={() => setDisplayUpdateModal(r)}></EditIcon>,
        },
        {
            name: 'Locations',
            maxWidth: "3%",
            minWidth: "70px",
            style: TableIconsStyles,
            cell: (r) => <ListIcon variant="info" onClick={() => setDisplayBranchModal(r)}></ListIcon>,
        },
        {
            name: 'Disable',
            maxWidth: "3%",
            minWidth: "70px",
            style: TableIconsStyles,
            cell: (client) => {
                const disabldBtnTxt = client.disabled ? "Enable" : "Disable"

                return (
                    <DeleteIcon
                        variant="warning"
                        disbled={updateReq.loading}
                        onClick={() => {
                            setLoadingToggle(client)
                            doUpdate({ data: { id: client.id, disabled: !client.disabled } })
                                .then(() => {
                                    return refetch()
                                })
                                .then(() => setLoadingToggle(false))
                        }}
                    >{loadingToggle.id === client.id ? "Wait..." : disabldBtnTxt}</DeleteIcon>
                )
            }
        },
    ];


    let Body = null

    if (getClientsReq.loading) {
        Body = (<>
            <CircularProgress />
        </>)
    } else if (getClientsReq.data) {
        Body = (
            <Col span={24}>
                <DataTable
                subHeaderAlign="center"
                    title="Brokers"
                    actions={<Button variant="contained" color="primary" onClick={() => setDisplayModal(true)}>Add Broker</Button>}
                    columns={columns}
                    data={getClientsReq.data}
                />
            </Col>
        );
    } else if (getClientsReq.error) {
        Body = (
            <>
                <h3>Error fetching the resource</h3>
            </>
        );
    }

    return (
        <Container fluid>
            <Row>
                {Body}
            </Row>
            {displayBranchModal && <LocationListModal broker={displayBranchModal} refetch={refetch} handleClose={() => {
                refetch()
                setDisplayBranchModal(false)
            }} />}
            {displayModal && <CreateBrokerModal refetch={refetch} handleClose={() => setDisplayModal(false)} />}
            {displayUpdateModal && <CreateBrokerModal broker={displayUpdateModal} refetch={refetch} handleClose={() => setDisplayUpdateModal(false)} />}
        </Container>
    );
}
