import React, { useState, useEffect } from 'react';
import { TextField, Grid, Paper, FormControlLabel, Checkbox, Typography, Select, MenuItem } from '@material-ui/core';
import { postCategories, getCategories, deleteCategory } from "../../crud/categories.crud";
import { getFields } from "../../crud/scraper.crud";
import AxioHook from 'axios-hooks'
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import AddIcon from '@material-ui/icons/Add';
import CircularProgress from '@material-ui/core/CircularProgress';
import {
  Button,
  Modal,
} from "react-bootstrap";
import { createGlobalState } from 'react-hooks-global-state';
import { useAppState } from '../AppState';

const initialState = { inputCounter: [0], categoriesToSend: [], loading: false };
const { useGlobalState: useFormState, setGlobalState } = createGlobalState(initialState);
const resetState = () => {
  setGlobalState('inputCounter', initialState.inputCounter);
  setGlobalState('categoriesToSend', initialState.categoriesToSend);
}

const FormInput = ({ idx, id, name, disabled, responseProperty,onDeleted, options, type }) => {
  const [, setInputCounter] = useFormState('inputCounter');
  const [, setCategoriesToSend] = useFormState('categoriesToSend');

  const [deleteCategoryReq, doDeleteCategory] = AxioHook(deleteCategory(), { manual: true })
  const defaultValues = { idx: idx, disabled: false };

  useEffect(() => {
    if (id) {
      defaultValues.id = id
      defaultValues.idx = id
    }
    if (type) defaultValues.type = type
    if (name) defaultValues.name = name
    if (disabled) defaultValues.disabled = disabled
    if (responseProperty) defaultValues.responseProperty = responseProperty

    setCategoriesToSend(prev => [
      defaultValues,
      ...prev]);
  }, []);

  return (
    <Paper>
      <Grid container spacing={1} alignItems="center">

        <Grid item xs={3}>
          <TextField
            style={{ width: '100%' }}
            defaultValue={name}
            onChange={(e) => {
              e.persist();
              setCategoriesToSend(prev => {
                const found = prev.find(v => v.idx === defaultValues.idx);
                if (found) return [{ ...found, name: e.currentTarget.value }, ...prev.filter(v => v.idx !== found.idx)]
                return prev
              });
            }} placeholder="Category" />
        </Grid>

        <Grid item xs={3}>
          <Select
            labelId="demo-simple-select-label"
            style={{ width: '100%' }}
            defaultValue={responseProperty}
            onChange={(e) => {
              setCategoriesToSend(prev => {
                const found = prev.find(v => v.idx === defaultValues.idx);
                if (found) return [{ ...found, responseProperty: e.target.value }, ...prev.filter(v => v.idx !== found.idx)]
              });
            }}
          >
            {options.map(f => <MenuItem key={f.name} value={f.name}>{f.name}</MenuItem>)}
          </Select>
        </Grid>

        <Grid item xs={2}>
          <Select
            labelId="demo-simple-select-label"
            style={{ width: '100%' }}
            defaultValue={type}
            onChange={(e) => {
              setCategoriesToSend(prev => {
                const found = prev.find(v => v.idx === defaultValues.idx);
                if (found) return [{ ...found, type: e.target.value }, ...prev.filter(v => v.idx !== found.idx)]
              });
            }}
          >
            <MenuItem value="tag">Tag</MenuItem>
            <MenuItem value="number">Number</MenuItem>
            <MenuItem value="range">Range</MenuItem>
          </Select>
        </Grid>

        <Grid item style={{ cursor: 'pointer' }} >
          <FormControlLabel
            xs={2}
            control={<Checkbox
              defaultChecked={disabled}
              onChange={() => {
                setCategoriesToSend(prev => {
                  const found = prev.find(v => v.idx === defaultValues.idx);
                  if (found) return [{ ...found, disabled: !found.disabled }, ...prev.filter(v => v.idx !== found.idx)]
                });
              }} />}
            label="Disabled"
          />
        </Grid>

        <Grid item xs={1} style={{ cursor: 'pointer', display: 'flex', justifyContent: 'center' }}
          onClick={() => setInputCounter(prev => [...prev, prev[prev.length - 1] + 1])} >
          <AddIcon />
        </Grid>
        <Grid item
          xs={1}
          style={{ cursor: 'pointer', display: 'flex', justifyContent: 'center' }}
          onClick={async () => {
            if (id) await doDeleteCategory({ data: { id: id } });
            setCategoriesToSend(prev => {
              const found = prev.find(v => v.idx === id);
              if (found && found.id) {
                onDeleted && onDeleted()
              }
              if (found) return prev.filter(v => v.idx !== found.idx)
              return prev;
            });
            setInputCounter(prev => {
              const found = prev.find(v => v === idx);
              if (found !== undefined) return prev.filter(v => v !== found)
              return prev
            });
          }}
        >
          {deleteCategoryReq.loading ? <CircularProgress size={20} /> : <DeleteForeverIcon />}
        </Grid>
      </Grid>
    </Paper>
  );
}

export const CreateCategoryModal = ({ handleClose, offering }) => {
  const [inputCounter, setInputCounter] = useFormState('inputCounter');
  const [categoriesToSend] = useFormState('categoriesToSend');
  const [, setSuccess] = useAppState('success');

  const [responseFieldsReq] = AxioHook(getFields())
  const [postCategoriesReq, doPostCategories] = AxioHook(postCategories(), { manual: true })
  const [getCategoriesReq, refetch] = AxioHook(getCategories(offering))

  useEffect(() => resetState, []);

  let Body = (<CircularProgress />)

  if (getCategoriesReq.loading && responseFieldsReq.loading) {
    Body = (<CircularProgress />)
  } else if (getCategoriesReq.data && responseFieldsReq.data) {
    Body = (
      <>
        {getCategoriesReq.data && getCategoriesReq.data.map(c => {
          return <FormInput options={responseFieldsReq.data} key={c.id} idx={c.id} {...c} onDeleted={refetch} />
        })}
        {inputCounter.map((id) => {
          return <FormInput options={responseFieldsReq.data} key={id} idx={id} />
        })}
      </>
    );
  } else if (getCategoriesReq.error || responseFieldsReq.error) {
    Body = (
      <Typography>
        Error fetching the resource
      </Typography>
    );
  }

  return (
    <Modal size="lg" show={true} onHide={() => handleClose('hide')}>
      <Modal.Header closeButton>
        <Modal.Title>Category for {offering}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {Body}
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={() => handleClose('close')}>
          Close
            </Button>
        <Button disabled={postCategoriesReq.loading} variant="primary" onClick={() => {
          const toSend = categoriesToSend.filter(v => v.name).map(v => ({ ...v, offering }))
          doPostCategories({ data: { categories: toSend } })
            .then(r => {
              setSuccess(r.data.success);
            })
          ///handleClose('save')
        }}>
          {postCategoriesReq.loading ? 'Saving...' : 'Save Changes'}
        </Button>
      </Modal.Footer>
    </Modal>
  );
}
